import { Component, OnInit } from '@angular/core';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Storage } from '@ionic/storage';
import { AngularFireDatabase} from 'angularfire2/database';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import * as $ from 'jquery';


@Component({
  selector: 'app-messaging',
  templateUrl: './messaging.page.html',
  styleUrls: ['./messaging.page.scss'],
})

export class MessagingPage implements OnInit {


  chatsRef$: Observable<any[]>;

  constructor(private statusBar: StatusBar,private storage: Storage,private database:AngularFireDatabase,private route: Router) { 
    this.statusBar.overlaysWebView(false);
    this.statusBar.styleDefault();
    this.chatsRef$ = this.database.list("chats").valueChanges();
  }

  ngOnInit() {
  }


  name: Observable<any>;
  sname: Observable<any>;
  receiverUsername: Observable<any>;
  this_username: Observable<any>;
  ionViewDidEnter() {
    // Put here the code you want to execute
    this.statusBar.overlaysWebView(false);
    this.statusBar.backgroundColorByHexString('#ffffff');
    this.statusBar.styleDefault();
    this.storage.get('mail').then((val) => {
      let profile_url =  'https://uploaded.herokuapp.com/users/users';
      //profile_url = 'http://127.0.0.1:8000/users/users'
      if(val == undefined){
        this.route.navigate(['']);
      }else{
        this.storage.get('username').then((val)=>{
          this.this_username = val;
        });
        this.getChat();
      }
    });
  }


  getChat(){
    this.storage.get("receiverName").then( (name)=>{
      this.name = name;
      console.log(name);
      this.storage.get("receiverSName").then( (sname)=>{
        this.sname = sname;
        this.storage.get("receiverID").then((id)=>{
          this.receiverUsername = id;
        });
      });
    });
  }


  sendMessage(){
    let messageInput = $("#message").val();
    console.log(messageInput);
    if(messageInput != ""){
      let message = { "sender":this.this_username, "to":this.receiverUsername, "message":messageInput };
      this.database.list("chats").push(message);
      $("#message").val("");
    }
  }

}
